.nav {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: rgb(218, 218, 218);
  letter-spacing: 0.15em;
  text-transform: uppercase;
  > .nav-brand > a {
    font-size: 30px;
    letter-spacing: 0.25em;
    cursor: pointer;
  }
  .nav-btn {
    display: none;
  }
  > ul {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    list-style: none;
    justify-content: flex-end;
    align-items: center;
    overflow: hidden;
    li {
      display: flex;
      text-align: center;
      padding: 1em;
      &:hover {
        color: white;
        cursor: pointer;
        font-weight: 600;
      }
    }
  }
}

@media (max-width: 800px) {
  .nav {
    .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding: 0 1em;
      #navbar-toggle {
        cursor: pointer;
        padding: 10px 35px 16px 0px;
        span,
        span:before,
        span:after {
          cursor: pointer;
          border-radius: 1px;
          height: 3px;
          width: 30px;
          background: #ffffff;
          position: absolute;
          display: block;
          content: "";
          transition: all 300ms ease-in-out;
        }
        span:before {
          top: -10px;
        }
        span:after {
          bottom: -10px;
        }
        &.active span {
          background-color: transparent;
          &:before {
            top: 0;
            transform: rotate(-45deg);
          }
          &:after {
            top: 0;
            transform: rotate(45deg);
          }
        }
      }
    }
    > ul {
      display: none;
    }
    > ul.active {
      animation: slide 0.5s forwards;
      display: flex;
      position: fixed;
      z-index: 1;
      width: 100%;
      top: 80px;
      right: -500px;
      height: calc(100% - 80px);
      justify-content: flex-start;
      flex-direction: column;
      align-items: flex-start;
      background: #2e2e2e;
      margin: 0;
      padding: 0;
      > li {
        font-size: 12px;
      }
    }
  }
}
